import React, { useState } from "react";
import { Link } from "react-router-dom";

export const App = () => {
  const [text, setText] = useState(" ");

  setTimeout(() => setText(" React-"), 1000);

  return (
    <div>
      {text}
      <Link to="/something">Something</Link>
      <Link to="/oders"></Link>
    </div>
  );
};
